import React, { Component } from 'react';
import Cookies from 'js-cookie';

const RegisterCookie = WrappedComponent => {
    return class HOC extends Component {
        registerCookie(value) {
            const currentCookie = Cookies.get('nxt');
            console.log('cookie', currentCookie);
            // debugger;
            if (currentCookie) {
                Cookies.set('nxt', {
                    p: [...JSON.parse(currentCookie).p, value],
                });
            } else {
                Cookies.set('nxt', { p: [value] });
            }
        }
        render() {
            return <WrappedComponent registerCookie={this.registerCookie} />;
        }
    };
};

export default RegisterCookie;



