import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './index.css';
import Cookies from 'js-cookie';

const data = [
    { position: 1, path: '/' },
    { position: 2, path: '/deux' },
    { position: 3, path: '/albert' },
    { position: 4, path: '/chichi' },
];

class Menu extends Component {
    constructor() {
        super();
        const cookie = Cookies.get('nxt');
        console.log('cookie', cookie);
        this.state = {
            value: '',
            display: false,
            passed: cookie && JSON.parse(cookie).p,
        };
        this.handleMenu = this.handleMenu.bind(this);
        // this.onChange = this.onChange.bind(this);
    }

    // onChange(e) {
    //     e.persist();
    //     const value = e.target.value;
    //     this.setState(() => ({ value }));
    //     // if () {
    //     //     this.setState(() => ({ display: true }));
    //     // } else if (this.state.display === true) {
    //     //     this.setState(() => ({ display: false }));
    //     // }
    // }

    handleMenu() {
        this.setState(() => ({ display: !this.state.display }));
    }

    renderLink(item) {
        console.log('passed', this.state.passed);
        // if (this.state.passed.includes(item)) {
        return (
            <div className="menu-link-container" key={item.position}>
                <Link
                    to={item.path}
                    onClick={this.handleMenu}
                    className="menu-link"
                >
                    <div
                        className={
                            this.state.passed && this.state.passed.includes(item.position)
                                ? 'ddd'
                                : ''
                        }
                        style={{
                            color: this.state.passed && this.state.passed.includes(item.position)
                                ? 'white'
                                : '#a2a2a2',
                            margin: '0 24px',
                        }}
                    >
                        {item.position}
                    </div>
                </Link>
            </div>
        );
        // }
    }

    render() {
        const { children } = this.props;
        return (
            <div className="menu-container">
                <button onClick={this.handleMenu} className="menu-burger">
                    
                </button>
                {this.state.display && (
                    <div className="rollin">
                        {data.map(value => this.renderLink(value))}
                    </div>
                )}
                {children}
            </div>
        );
    }
}

export default Menu;
